import { Injectable } from '@angular/core';
import { Vendor } from './vendor';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class VendorService {
  formData:Vendor;
  vendor:Vendor;
  vendors:Vendor;
  constructor(private httpService:HttpClient) { }
 
  // insertVendor(vendor: Vendor) {
  //   let body = JSON.stringify(vendor);
  //   console.log(vendor);
  //   let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
  //   let options = { headers: headers }
  //   console.log('before save');
  //   return this.httpService.post(environment.APIUrl + '/insertVendor', body, options);
  //   }
  getAllVendors():Observable<Vendor[]>{
    return this.httpService.get<Vendor[]>
    (environment.APIUrl+'/getallvendors');      
  }

  getVendorById(vId:any):Observable<Vendor>{
    return this.httpService.get<Vendor>
    (environment.APIUrl+'/vendors/'+vId)
  }

  disableVendor(vId:any){
    let body = JSON.stringify(vId);
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    let options = { headers: headers }
    return this.httpService.put<Vendor>(environment.APIUrl+'/disableVendor/'+vId,body,options);
  }
}
