import { Injectable } from '@angular/core';
import { Vendor } from '../vendor/vendor';

import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AddVendorService {
  formData: { vendorId: string; vendorName: string; vendorAddress: string; vendorLocation: string; vendorService: string; pincode: number; isActive: string; contactId: string; name: string; department: string; email: string; phoneNo: string; };

  constructor(private _httpService:HttpClient,private router:Router) { }

  getAllVendors():Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>
    (environment.APIUrl+'/vendor');      
  }

  insertVendor(vendor:Vendor){
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    console.log('YES');
    if(vendor.vendorId){
      return this._httpService.put(environment.APIUrl+'/updateVendor/',body,options);
    }
    else{
    
      return this._httpService.post(environment.APIUrl +'/insertVendor/', body, options);
    }
}

getVendorById(vId:any):Observable<Vendor>{
  return this._httpService.get<Vendor>
  (environment.APIUrl+'/vendors/'+vId)
}
}
