import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VendorService } from './vendor.service';
import { Vendor } from './vendor';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {
  vendors:Vendor[]
  vendor = new Vendor();
  view:any;
  searchId:any;

  constructor(private router:Router,private vendorService:VendorService) { }

  ngOnInit() {
    this.getAllVendors();
  

  }

  viewContact(contactId:any){
    // this.view=contactId.vendorId;
    // this.viewContact;
    // this.viewContact(this.view);
    this.router.navigate(['viewContact',contactId])
    
  }

  getAllVendors():void{
    this.vendorService.getAllVendors()
    .subscribe((vendordata)=>{
      this.vendors=vendordata;
      console.log(vendordata);

    },(error)=>{
      console.log(error);

    });


 }
    addVendor(){
      this.router.navigate(['saveVendor']);
    }

    editVendor(data:number){
      this.router.navigate(['editVendor',data])
    }


    onSelect(select:Vendor){
      this.searchId=select.vendorId;
      
    }

    disableVendor(){
      this.vendorService.disableVendor(this.searchId)
      .subscribe((response)=>{
        console.log(response);
        this.getAllVendors();
        this.router.navigate(['vendor']);
      })
    }

   logout(){
     this.router.navigate(['login'])
   }

}
