import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor/vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { AddVendorService } from './add-vendor.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {
  vendors:Vendor[];
  vendor=new Vendor();
  vendorId: any;
  vId:any;
  constructor(private router: Router,private service: AddVendorService, private route:ActivatedRoute) { }

  ngOnInit() {

    this.getId();
    this.route.params.subscribe( params => this.getVendorById(params['vendorId']));
  
  }
  getVendorById(arg0: any): void {
    throw new Error("Method not implemented.");
  }
  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  getId(){
    this.vId=this.route.snapshot.paramMap.get('id');
    this.service.getVendorById(this.vId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    })
  }

  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorId: '',
      vendorName:'',
      vendorAddress: '',
      vendorLocation: '',
      vendorService: '',
      pincode:null,
      isActive: '',
      contactId: '',
      name:'',
      department: '',
      email: '',
      phoneNo : ''           
    }                                  
  }

  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  getById(vendorId:any){
    this.service.getVendorById(vendorId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    },(error) =>{
      console.log(error);
    
    });
  }

  toBack(){
    this.router.navigate(['vendor']);
  }

}
